import React, { useState } from 'react';
import axios from 'axios';
import 'bootstrap/dist/css/bootstrap.min.css'; // Import Bootstrap CSS
import 'bootstrap/dist/js/bootstrap.bundle.min'; // Import Bootstrap JS
import './Home.css'; // Optional: Create a CSS file if you need custom styles
import { Link, useNavigate } from 'react-router-dom'; // Import Link for navigation

function AddStock() {
  const [userForm, setUserForm] = useState({
    name: "",
    unit: "",
    availability: "",
    price: "",
  });

  const navigate = useNavigate(); // Initialize useNavigate hook

  const inputsHandler = (e) => {
    setUserForm((prevNext) => ({
      ...prevNext,
      [e.target.name]: e.target.value,
    }));
  };

  const onSubmit = (e) => {
    e.preventDefault();
    axios
      .post("http://localhost:3001/admingrocery/addstock", userForm)
      .then((res) => {
        console.log(res.data);
        setUserForm({
          name: "",
          unit: "",
          availability: "",
          price: "",
        });
        // Navigate to the Stock List after successful submission
        navigate('/GeneralStock'); // Navigate to the stock list page
      })
      .catch((error) => {
        if (error.response) {
          // The request was made, but the server responded with a status code
          console.error("Error Response:", error.response.data);
          console.error("Error Status:", error.response.status);
        } else if (error.request) {
          // The request was made, but no response was received
          console.error("No Response:", error.request);
        } else {
          // Something else caused an error
          console.error("Error Message:", error.message);
        }
      });
  };

  return (
    <div className="container-well mt-4">
      <nav className="navbar navbar-expand-lg navbar-light bg-secondary">
        <div className="container-fluid">
          <h1>
            <a className="navbar-brand" href="#">Add Stock List</a>
          </h1>
          <button
            className="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarNavDropdown"
            aria-controls="navbarNavDropdown"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarNavDropdown">
            <ul className="navbar-nav">
              <li className="nav-item">
                <Link className="nav-link text-light" to="/GeneralStock" aria-current="page">STOCK LIST</Link>
              </li>
              <li className="nav-item">
                <a className="nav-link" href="#">Logout</a>
              </li>
            </ul>
          </div>
        </div>
      </nav>

      <div className=" addstock form-wrapper mt-4">
        <form onSubmit={onSubmit} >
          <div className="mb-3">
            <label className="form-label">Name</label>
            <input
              type="text"
              className="form-control"
              name="name"
              id="name"
              value={userForm.name}
              onChange={inputsHandler}
            />
          </div>
          <div className="mb-3">
            <label className="form-label">Unit</label>
            <input
              type="text"
              className="form-control"
              name="unit"
              id="unit"
              value={userForm.unit}
              onChange={inputsHandler}
            />
          </div>
          <div className="mb-3">
            <label className="form-label">Availability</label>
            <input
              type="text"
              className="form-control"
              name="availability"
              id="availability"
              value={userForm.availability}
              onChange={inputsHandler}
            />
          </div>
          <div className="mb-3">
            <label className="form-label">Price</label>
            <input
              type="text"
              className="form-control"
              name="price"
              id="price"
              value={userForm.price}
              onChange={inputsHandler}
            />
          </div>
          <div className="mb-3">
            <button type="submit" className="btn btn-primary">
              Add Stock
            </button>
          </div>
        </form>
      </div>
    </div>
  );
}

export default AddStock;
