import React, { useEffect, useState } from "react";
import axios from "axios";
import { Link, useNavigate } from "react-router-dom";
import * as XLSX from "xlsx"; // Import the XLSX library for handling Excel

function GeneralStock() {
  const [stocks, setStocks] = useState([]);
  const [loading, setLoading] = useState(true); // Loading state
  const [error, setError] = useState(null); // Error state
  const [successMessage, setSuccessMessage] = useState(null); // Success message state
  const [imageUrl, setImageUrl] = useState(""); // State to hold image URL for new stock
  const [newStock, setNewStock] = useState({
    name: "",
    unit: "",
    availability: 0,
    price: 0,
  });

  const navigate = useNavigate(); // React Router's useNavigate hook

  // Fetch all stocks when the component mounts
  useEffect(() => {
    axios
      .get("http://localhost:3001/admingrocery/generalstock")
      .then((res) => {
        setStocks(res.data.data);
        setLoading(false);
      })
      .catch((err) => {
        setError("Error fetching stock list.");
        setLoading(false);
        console.error("Error fetching stock list:", err);
      });
  }, []);

  // Handle delete functionality
  const handleDelete = (id) => {
    if (window.confirm("Are you sure you want to delete this stock?")) {
      axios
        .delete(`http://localhost:3001/admingrocery/deletestock/${id}`)
        .then((res) => {
          setStocks((prevStocks) =>
            prevStocks.filter((stock) => stock._id !== id)
          );
          alert("Stock deleted successfully");
        })
        .catch((err) => {
          console.error("Error deleting stock:", err);
          alert("Error deleting stock");
        });
    }
  };

  // Handle the form submission for adding new stock
  const handleSubmit = (e) => {
    e.preventDefault();
    axios
      .post("http://localhost:3001/admingrocery/addstock", {
        ...newStock,
        imageUrl: imageUrl,
      })
      .then((res) => {
        setSuccessMessage("Stock added successfully!");
        setStocks((prevStocks) => [...prevStocks, res.data.data]);
        setNewStock({ name: "", unit: "", availability: 0, price: 0 });
        setImageUrl(""); // Reset image URL after submitting
      })
      .catch((err) => {
        setError("Error adding stock.");
        console.error("Error adding stock:", err);
      });
  };

  // Function to export stocks to Excel
  const exportToExcel = () => {
    const ws = XLSX.utils.json_to_sheet(stocks); // Convert JSON data to Excel worksheet
    const wb = XLSX.utils.book_new(); // Create a new workbook
    XLSX.utils.book_append_sheet(wb, ws, "Stock List"); // Append worksheet to workbook
    XLSX.writeFile(wb, "GeneralStockList.xlsx"); // Trigger download as Excel file
  };

  return (
    <div className="container-fluid mt-4">
      {/* Navbar */}
      <nav className="navbar navbar-expand-lg navbar-light bg-success w-100">
        <div className="container-fluid">
          <h1>
            <a className="navbar-brand" href="#">
              General Stock List
            </a>
          </h1>
        </div>
      </nav>

      {/* Success Message */}
      {successMessage && (
        <div className="alert alert-success" role="alert">
          {successMessage}
        </div>
      )}

      {/* Loading indicator */}
      {loading ? (
        <div className="loading-spinner">
          <p>Loading stocks...</p>
        </div>
      ) : error ? (
        <div className="alert alert-danger" role="alert">
          {error}
        </div>
      ) : (
        <div>
          <h1>Stock List</h1>

          {/* Export to Excel Button */}
          <button className="btn btn-primary mb-3" onClick={exportToExcel}>
            Export to Excel
          </button>

          {/* Add Stock Form */}
          <form onSubmit={handleSubmit} className="mb-4">
            <input
              type="text"
              className="form-control mb-2"
              placeholder="Stock Name"
              value={newStock.name}
              onChange={(e) =>
                setNewStock({ ...newStock, name: e.target.value })
              }
              required
            />
            <input
              type="text"
              className="form-control mb-2"
              placeholder="Unit"
              value={newStock.unit}
              onChange={(e) =>
                setNewStock({ ...newStock, unit: e.target.value })
              }
              required
            />
            <input
              type="number"
              className="form-control mb-2"
              placeholder="Availability"
              value={newStock.availability}
              onChange={(e) =>
                setNewStock({
                  ...newStock,
                  availability: e.target.value,
                })
              }
              required
            />
            <input
              type="number"
              className="form-control mb-2"
              placeholder="Price"
              value={newStock.price}
              onChange={(e) =>
                setNewStock({ ...newStock, price: e.target.value })
              }
              required
            />
            <input
              type="text"
              className="form-control mb-2"
              placeholder="Image URL"
              value={imageUrl}
              onChange={(e) => setImageUrl(e.target.value)}
              required
            />
            <button type="submit" className="btn btn-success">
              Add Stock
            </button>
          </form>

          {/* Table for stocks */}
          <table className="table table-bordered ">
            <thead>
              <tr>
                <th>Name</th>
                <th>Unit</th>
                <th>Availability</th>
                <th>Price</th>
                <th>Image</th>
                <th>Actions</th>
              </tr>
            </thead>
            <tbody>
              {stocks.map((stock) => (
                <tr key={stock._id}>
                  <td>{stock.name}</td>
                  <td>{stock.unit}</td>
                  <td>{stock.availability}</td>
                  <td>{stock.price}</td>
                  <td>
                    <img
                      src={stock.imageUrl}
                      alt={stock.name}
                      style={{ width: "50px", height: "50px" }}
                    />
                  </td>
                  <td>
                    {/* Edit button */}
                    <Link
                      to={`/edit-stock/${stock._id}`}
                      className="edit-btn btn btn-warning mr-2"
                    >
                      Edit Stock
                    </Link>
                    {/* Delete button */}
                    <button
                      className="dlt-btn btn btn-danger"
                      onClick={() => handleDelete(stock._id)}
                    >
                      Delete Stock
                    </button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      )}
    </div>
  );
}

export default GeneralStock;
