import { Link, useNavigate } from 'react-router-dom'; // Import Link and useNavigate from react-router-dom
import React, { useState } from 'react';
import axios from 'axios';
import './Home.css';

function Login() {
    const navigate = useNavigate();
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [errorMessage, setErrorMessage] = useState(''); // State for handling error message
    const [loading, setLoading] = useState(false); // State for loading status

    // Handle form submission
    const handleSubmit = async (e) => {
        e.preventDefault();

        // Input validation
        if (!email || !password) {
            setErrorMessage('Both fields are required!');
            return;
        }

        setLoading(true);
        try {
            const response = await axios.post('http://localhost:3001/login', { email, password });
            console.log(response);
            alert('Login Successful');
            navigate('/greengrocery'); // Redirect to home page after successful login
        } catch (err) {
            console.error(err);
            setErrorMessage('Login Failed. Please check your credentials.'); // Show specific error message
        } finally {
            setLoading(false); // Reset loading state after request is done
        }
    };

    return (
        <div className="login-container">
            <nav className="navbar navbar-expand-lg navbar-light bg-info" >
                <div className="container-md">
                    <a className="navbar-brand text-light" href="#">GᖇEEᑎ GᖇOᑕEᖇIEᔕ</a>
                </div>
            </nav>

            <div className="container-well">
                <div className="login-page">
                    <div className="col-sm-5 offset-sm-2 col-md-6 offset-md-0">
                        <div className="d-flex justify-content-center align-items-center vw-100">
                        <div className="bg-warning p-3 rounded w-50 mt-5">
                                <div className="login-form">
                                    <h2 style={{ color: 'black' }}>WELCOME</h2>

                                    {/* Form */}
                                    <form onSubmit={handleSubmit}>
                                        {/* Email Input */}
                                        <div className="form-floating mb-3">
                                            <input
                                                type="email"
                                                className="form-control rounded-2"
                                                id="floatingInput"
                                                placeholder="name@example.com"
                                                autoComplete="off"
                                                value={email}
                                                onChange={(e) => setEmail(e.target.value)}
                                            />
                                            <label htmlFor="floatingInput">Email address</label>
                                        </div>

                                        {/* Password Input */}
                                        <div className="form-floating mb-3">
                                            <input
                                                type="password"
                                                className="form-control rounded-2"
                                                id="floatingPassword"
                                                placeholder="Password"
                                                value={password}
                                                onChange={(e) => setPassword(e.target.value)}
                                            />
                                            <label htmlFor="floatingPassword">Password</label>
                                        </div>

                                        {/* Error Message */}
                                        {errorMessage && (
                                            <div className="alert alert-danger" role="alert">
                                                {errorMessage}
                                            </div>
                                        )}

                                        {/* Submit Button */}
                                        <button
                                            type="submit"
                                            className="btn btn-warning w-100 rounded-2"
                                            disabled={loading} // Disable button while loading
                                        >
                                            {loading ? 'Logging in...' : 'Login'}
                                        </button>

                                        <br />
                                        <br />

                                        {/* Signup Link */}
                                        <Link className="btn btn-primary w-100 rounded-2" to="/Register">
                                            Sign Up
                                        </Link>
                                    </form>

                                    <p className="text-center mt-3">Don't have an account?</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Login;
