import React, { useState } from "react";
import axios from 'axios';
import { useNavigate } from "react-router-dom";

function UserSignup() {
    const navigate = useNavigate();

    const [name, setName] = useState("");
    const [email, setEmail] = useState("");
    const [mobilenumber, setMobilenumber] = useState("");
    const [password, setPassword] = useState("");

    const handleSubmit = (e) => {
        e.preventDefault();
    
        // Construct request data
        const userData = { name, email, mobilenumber, password };
    
        // Make the API request to register
        axios.post('https://grocerya-backend.onrender.com/register', userData, {
            headers: {
                'Content-Type': 'application/json',
            }
        })
        .then(result => {
            console.log(result);
            alert("Registered Successfully!");
            navigate("/user/Login");  // Navigate after a successful registration
        })
        .catch(err => {
            console.log("Error:", err.response ? err.response.data : err.message);
            alert("Registration failed. Please try again.");
        });
    };
    return (
        <div>
            <div className="register">
                <div className="d-flex justify-content-center align-items-left vw-80 mt-10">
                    <div className="sign-form p-3 rounded w-50">
                        <h2 style={{ color: "black" }}>Register | Green Groceries</h2>
                        <form onSubmit={handleSubmit}>
                            <div className="mb-3">
                                <label htmlFor="name"><strong>NAME</strong></label>
                                <input
                                    type="text"
                                    placeholder="Enter Name"
                                    autoComplete="off"
                                    name="name"
                                    className="form-control rounded-0"
                                    onChange={(e) => setName(e.target.value)}
                                />
                            </div>

                            <div className="mb-3">
                                <label htmlFor="email"><strong>Email</strong></label>
                                <input
                                    type="email"
                                    placeholder="Enter Email"
                                    autoComplete="off"
                                    name="email"
                                    className="form-control rounded-0"
                                    onChange={(e) => setEmail(e.target.value)}
                                />
                            </div>

                            <div className="mb-3">
                                <label htmlFor="mobilenumber"><strong>Mobile Number</strong></label>
                                <input
                                    type="text"
                                    placeholder="Mobile Number"
                                    name="mobilenumber"
                                    className="form-control rounded-0"
                                    onChange={(e) => setMobilenumber(e.target.value)}
                                />
                            </div>

                            <div className="mb-3">
                                <label htmlFor="password"><strong>Password</strong></label>
                                <input
                                    type="password"
                                    placeholder="Enter password"
                                    name="password"
                                    className="form-control rounded-0"
                                    onChange={(e) => setPassword(e.target.value)}
                                />
                            </div>

                            <button type="submit" className="btn btn-success w-100 rounded-0">
                                Register
                            </button>
                        </form>

                        <p className="d-flex justify-content-center">
                            Already have an Account?
                        </p>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default UserSignup;
