import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Routes, Route } from 'react-router-dom';
import ProductCard from './UserDashboard/ProductCard';
import Signup from './UserDashboard/Signup';
import Login from './UserDashboard/Login';
import GeneralStock from './AdminDashboard/GeneralStock';
import AddStock from './AdminDashboard/AddStock';
import EditStock from './AdminDashboard/EditStock';
import AdminProductcard from './AdminDashboard/AdminProductcard';
import AdminLogin from './AdminDashboard/AdminLogin';
import AdminSignup from './AdminDashboard/AdminSignup';
import UserCart from './UserDashboard/Cart';
import 'bootstrap-icons/font/bootstrap-icons.css';
import { useState } from 'react';
import NavBar from './Components/NavBar';

const App = () => {
  const [cartItems, setCartItems] = useState([]); // Cart items state
  const [searchTerm, setSearchTerm] = useState(''); // Search term state

  // Add item to cart
  const addToCart = (product) => {
    setCartItems((prevCartItems) => {
      const existingItem = prevCartItems.find((item) => item.id === product.id);
      if (existingItem) {
        return prevCartItems.map((item) =>
          item.id === product.id
            ? { ...item, quantity: item.quantity + 1 }
            : item
        );
      }
      return [...prevCartItems, { ...product, quantity: 1 }];
    });
    alert(`${product.name} added to cart`);
  };

  // Update item quantity in the cart
  const updateQuantity = (id, change) => {
    setCartItems((prevCartItems) => {
      return prevCartItems
        .map((item) => {
          if (item.id === id) {
            const newQuantity = item.quantity + change;
            return newQuantity > 0 ? { ...item, quantity: newQuantity } : null;
          }
          return item;
        })
        .filter(Boolean); // Remove null items (items with quantity 0)
    });
  };

  // Remove item from the cart
  const removeFromCart = (id) => {
    setCartItems((prevCartItems) => prevCartItems.filter((item) => item.id !== id));
  };

  return (
    <div className="App">
      {/* Include NavBar globally */}
      <NavBar />

      {/* Define the Routes */}
      <Routes>
     
        {/* admin Routes */}
        <Route path="/admin/productcard" element={<AdminProductcard />} />

        <Route path="/register" element={<Signup />} />
        <Route path="/admin/login" element={<AdminLogin />} />
        <Route path="/admin/register" element={<AdminSignup />} />
        <Route path="/GeneralStock" element={<GeneralStock addToCart={addToCart} />} />
        <Route path="/addstock" element={<AddStock />} />
        <Route path="/edit-stock/:id" element={<EditStock />} />

        {/* user Routes */}
        {/* You might not need a separate /navbar route if NavBar is global */}
        <Route path="/greengrocery" element={<ProductCard addToCart={addToCart} searchTerm={searchTerm} />} />
        <Route path="/user/signup" element={<Signup />} />
        <Route path="/user/login" element={<Login />} />
        <Route path="/usercart" element={<UserCart cartItems={cartItems} updateQuantity={updateQuantity} removeFromCart={removeFromCart} />} />

      </Routes>
    </div>
  );
};

export default App;
