import React, { useEffect, useState } from "react";
import { useParams, useNavigate } from "react-router-dom";
import axios from "axios";
import './Home.css';
function EditStock() {
  const [userForm, setUserForm] = useState({
    name: "",
    unit: "",
    availability: "",
    price: "",
  });

  let params = useParams();
  let navigate = useNavigate(); // Initialize the navigate function

  // Handle form inputs
  const inputsHandler = (e) => {
    setUserForm((prevNext) => ({
      ...prevNext,
      [e.target.name]: e.target.value,
    }));
  };

  // Handle form submission
  const onUpdate = (e) => {
    e.preventDefault(); // Prevent default form submission

    // Send PUT request to update stock
    axios
      .put("http://localhost:3001/admingrocery/editstock/" + params.id, {
        name: userForm.name,
        unit: userForm.unit,
        availability: userForm.availability,
        price: userForm.price,
      })
      .then((res) => {
        // Notify user of success
        alert("Stock updated successfully");

        // Redirect to the stock list page
        navigate("/GeneralStock"); // Redirect to the GeneralStock (stock-list) page
      })
      .catch((err) => {
        console.error("Error updating stock:", err);
        alert("Failed to update stock");
      });
  };

  // Fetch stock details when the component mounts
  useEffect(() => {
    axios
      .get("http://localhost:3001/admingrocery/editstock/" + params.id)
      .then((res) => {
        setUserForm({
          name: res.data.data.name,
          unit: res.data.data.unit,
          availability: res.data.data.availability, // Correct field name
          price: res.data.data.price,
        });
      })
      .catch((err) => {
        console.error("Error fetching stock details:", err);
      });
  }, [params.id]); // Dependency on `params.id`

  return (
    <div>
      <h1>Update Stock</h1>
      <div className="update-stock form-wrapper">
        <form onSubmit={onUpdate}>
          <div className="mb-3">
            <label className="form-label">Name</label>
            <input
              type="text"
              className="form-control"
              name="name"
              id="name"
              value={userForm.name}
              onChange={inputsHandler}
            />
          </div>
          <div className="mb-3">
            <label className="form-label">Unit</label>
            <input
              type="text"
              className="form-control"
              name="unit"
              id="unit"
              value={userForm.unit}
              onChange={inputsHandler}
            />
          </div>
          <div className="mb-3">
            <label className="form-label">Availability</label>
            <input
              type="text"
              className="form-control"
              name="availability"
              id="availability"
              value={userForm.availability}
              onChange={inputsHandler}
            />
          </div>
          <div className="mb-3">
            <label className="form-label">Price</label>
            <input
              type="text"
              className="form-control"
              name="price"
              id="price"
              value={userForm.price}
              onChange={inputsHandler}
            />
          </div>
          <div className="mb-3">
            <button type="submit" className="btn btn-success">
              Update
            </button>
          </div>
        </form>
      </div>
    </div>
  );
}

export default EditStock;
