import React, { useEffect, useState } from 'react';
import './Cart.css';

const UserCart = () => {
  const [cart, setCart] = useState([]);
  const [address, setAddress] = useState('');
  const [mobileNumber, setMobileNumber] = useState('');
  const [isCheckout, setIsCheckout] = useState(false);
  const [orderSubmitted, setOrderSubmitted] = useState(false);

  useEffect(() => {
    const storedCart = JSON.parse(localStorage.getItem('cart')) || [];
    setCart(storedCart);
  }, []);

  const totalPrice = cart.reduce((total, item) => total + item.price * item.quantity, 0);

  const removeFromCart = (id) => {
    const updatedCart = cart.filter(item => item.id !== id);
    setCart(updatedCart);
    localStorage.setItem('cart', JSON.stringify(updatedCart));
  };

  const increaseQuantity = (id) => {
    const updatedCart = cart.map(item =>
      item.id === id ? { ...item, quantity: item.quantity + 1 } : item
    );
    setCart(updatedCart);
    localStorage.setItem('cart', JSON.stringify(updatedCart));
  };

  const decreaseQuantity = (id) => {
    const updatedCart = cart.map(item =>
      item.id === id && item.quantity > 1 ? { ...item, quantity: item.quantity - 1 } : item
    ).filter(item => item.quantity > 0);
    setCart(updatedCart);
    localStorage.setItem('cart', JSON.stringify(updatedCart));
  };

  const handleCheckout = () => {
    setIsCheckout(true);
  };

  const handleOrderSubmit = (e) => {
    e.preventDefault();
    // Add order submission logic here, e.g., send data to the backend
    setOrderSubmitted(true);
    localStorage.removeItem('cart'); // Clear the cart after order submission
    setCart([]); // Clear the local cart state
  };

  return (
    <div className="cart-container">
      <h2 className="text-center">Your Cart</h2>
      {cart.length === 0 ? (
        <p className="text-center">Your cart is empty.</p>
      ) : (
        <div>
          {cart.map((item) => (
            <div className="cart-item" key={item.id}>
              <h5>{item.name}</h5>
              <p>Price: ₹{item.price}</p>
              <p>Quantity: {item.quantity}</p>
              <button className="btn btn-secondary" onClick={() => increaseQuantity(item.id)}>Add</button>
              <button className="btn btn-danger" onClick={() => decreaseQuantity(item.id)} disabled={item.quantity <= 1}>Remove</button>
              <button 
  className="btn btn-danger" 
  onClick={() => {
    const confirmRemoval = window.confirm(`Are you sure you want to remove ${item.name} from the cart?`);
    if (confirmRemoval) {
      alert(`${item.name} has been removed from the cart.`);
      removeFromCart(item.id);
    }
  }}
>
  Remove from Cart
</button>
            </div>
          ))}
          <h4 className="total-price text-right">Total Price: ₹{totalPrice}</h4>
          <button className="btn btn-primary" onClick={handleCheckout}>Proceed to Checkout</button>
        </div>
      )}

      {isCheckout && !orderSubmitted && (
        <div className="checkout-form">
          <h3>Checkout</h3>
          <form onSubmit={handleOrderSubmit}>
            <div className="form-group">
              <label>Address</label>
              <input
                type="text"
                className="form-control"
                value={address}
                onChange={(e) => setAddress(e.target.value)}
                required
              />
            </div>
            <div className="form-group">
              <label>Mobile Number</label>
              <input
                type="tel"
                className="form-control"
                value={mobileNumber}
                onChange={(e) => setMobileNumber(e.target.value)}
                required
                pattern="[0-9]{10}"
              />
            </div>
            <button type="submit" className="btn btn-success">Submit Order</button>
          </form>
        </div>
      )}

      {orderSubmitted && (
        <div className="order-confirmation text-center">
          <h3>Order Confirmed!</h3>
          <p>Thank you for your purchase. Your order will be delivered to:</p>
          <p><strong>Address:</strong> {address}</p>
          <p><strong>Mobile Number:</strong> {mobileNumber}</p>
        </div>
      )}
    </div>
  );
};

export default UserCart;
