import React from 'react';
import { Link, useLocation } from 'react-router-dom';
import './NavBar.css';

const NavBar = () => {
  const location = useLocation();

  // Only render the side nav when on the /navbar page
  if (location.pathname !== '/') {
    return null;
  }

  return (
    <div className="main-nav">
      <div className="side-nav">
        <div className="container-fluid">
          <h3 className="text-center text-white my-4">GᖇEEᑎ GᖇOᑕEᖇIEᔕ</h3>

          {/* Embed Image */}
        

          <ul className="navbar-nav">
            <li className="nav-item">
              <Link className="nav-link" to="/greengrocery">
                <i className="bi bi-house-door"></i> User Dashboard
              </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/user/login">
                <i className="bi bi-box"></i> User Login
              </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/admin/productcard">
                <i className="bi bi-cart"></i> Admin Dashboard
              </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/admin/login">
                <i className="bi bi-person"></i> Admin Login
              </Link>
            </li>
          </ul>
        </div>
      </div>
      
    </div>
  );
};

export default NavBar;
