import { Link, useNavigate } from 'react-router-dom'; // Import Link and useNavigate from react-router-dom
import React, { useState } from 'react';
import axios from 'axios';
import './Home.css';
function UserLogin() {
    const navigate = useNavigate();
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');

    const handleSubmit = async (e) => {
        e.preventDefault();

        try {
            const response = await axios.post('http://localhost:3001/login', { email, password });
            console.log(response);
            alert('Login Successful');
            navigate('/admin/productcard');
        } catch (err) {
            console.error(err);
            alert('Login Failed');
        }
    };

    return (
        <div className="login-container">
            <nav className="navbar navbar-expand-lg navbar-light bg-info">
                <div className="container-md">
                    <a className="navbar-brand text-light" href="#">GᖇEEᑎ GᖇOᑕEᖇIEᔕ</a>
                </div>
            </nav>
            
            <div className="container-well" >
              
                    <div className="login-page">
                    <div className="col-sm-5 offset-sm-2 col-md-6 offset-md-0">
                        <div className="d-flex justify-content-center align-items-center vw-100">
                            <div className="bg-white p-3 rounded w-50">
                                <div className="login-form">
                                    <h2 style={{ color: 'black' }}>WELCOME</h2>
                                    <form onSubmit={handleSubmit}>
                                        <div className="form-floating mb-3">
                                            <input
                                                type="email"
                                                className="form-control rounded-2"
                                                id="floatingInput"
                                                placeholder="name@example.com"
                                                autoComplete="off"
                                                value={email}
                                                onChange={(e) => setEmail(e.target.value)}
                                            />
                                            <label htmlFor="floatingInput">Email address</label>
                                        </div>
                                        <div className="form-floating">
                                            <input
                                                type="password"
                                                className="form-control rounded-2"
                                                id="floatingPassword"
                                                placeholder="Password"
                                                value={password}
                                                onChange={(e) => setPassword(e.target.value)}
                                            />
                                            <label htmlFor="floatingPassword">Password</label>
                                        </div> <br></br>
                                        <button type="submit" className="btn btn-warning w-100 rounded-2">
                                            Login
                                        </button>
                                        <br />
                                        <br />
                                        <Link className="btn btn-primary w-100 rounded-2" to="/Register">
                                            Sign Up
                                        </Link>
                                    </form>
                                    <p className="text-center mt-3">Already have an account?</p>
                                </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
          
           
        </div>
    );
}

export default UserLogin;
